h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

body.modal-open {
  overflow: hidden;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (max-width: 767px) {
  .container {
    max-width: 320px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    min-width: 1200px;
    padding-left: 15px;
    padding-right: 16px;
  }
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.button-big {
  cursor: pointer;
  border: 0;
  border-radius: 22px;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  display: inline-block;
}

.button-normal {
  cursor: pointer;
  border: 0;
  border-radius: 20px;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28;
  display: inline-block;
}

.button-micro {
  cursor: pointer;
  border: 0;
  border-radius: 20px;
  font-family: DM Sans;
  font-size: 8px;
  font-weight: 400;
  line-height: 1.25;
  display: inline-block;
}

